.watch {
  border: 3px dashed #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  margin: 30px auto;
  position: relative;
}

@keyframes rotate {
  from {
    transform: translateX(-50%)rotate(0);
  }

  to {
    transform: translateX(-50%)rotate(360deg);
  }
}

.watch:after {
  content: "";
  background-color: #000;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.watch__hand {
  transform-origin: 50% 100%;
  position: absolute;
  bottom: 50%;
  left: 50%;
}

.watch__hand--second {
  background-color: #9e77f3;
  width: 3px;
  height: 250px;
  animation: 5s linear infinite rotate;
  transform: translateX(-50%);
}

.watch__hand--minute {
  background-color: #5205f8;
  width: 6px;
  height: 150px;
  animation: 60s steps(60, end) infinite rotate;
  transform: translateX(-50%);
}

html, body {
  margin: 0;
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

